/* ## Comments
--------------------------------------------- */

.comment-respond,
.entry-comments,
.entry-pings {
	background-color: #fff;
	font-size: 16px;
	font-size: 1.6rem;
	margin-bottom: 40px;
}

.comment-respond,
.entry-pings {
	padding: 60px 60px 32px;
}

.entry-comments {
	padding: 60px;
}

.comment-list {
	li {
		padding: 40px 0 0 30px;
	}

	.depth-1 {
		padding-left: 0;
	}
}

.comment-header {
	margin-bottom: 30px;
}

.comment-content {
	clear: both;
	ul > li {
		list-style-type: disc;
	}
}

.comment-respond {
	input[type="email"],
	input[type="text"],
	input[type="url"] {
		width: 50%;
	}

	label {
		display: block;
		margin-right: 12px;
	}
}

.comment-header p {
	margin-bottom: 0;
}

.entry-pings .reply {
	display: none;
}

@media only screen and (max-width: 860px) {

	.comment-respond,
	.entry-comments,
	.entry-pings {
		padding: 30px;
		margin-bottom: 5%;
	}

}
