/* # Site Navigation
---------------------------------------------------------------------------------------------------- */

.genesis-nav-menu {
	clear: both;
	font-size: 16px;
	font-size: 1.6rem;
	font-weight: 600;
	line-height: 1;
	width: 100%;

	.menu-item {
		display: inline-block;
		margin-bottom: 0;
		padding-bottom: 0;
		text-align: left;
	}

	a {
		color: #333;
		text-decoration: none;
		display: block;
		padding: 32px 20px;
	}

	a:hover,
	a:focus,
	.current-menu-item > a,
	.sub-menu .current-menu-item > a:hover,
	.sub-menu .current-menu-item > a:focus  {
		color: #c3251d;
		text-decoration: underline;
	}

	.sub-menu {
		border-top: 1px solid #eee;
		left: -9999px;
		opacity: 0;
		position: absolute;
		-webkit-transition: opacity .4s ease-in-out;
		-moz-transition:    opacity .4s ease-in-out;
		-ms-transition:     opacity .4s ease-in-out;
		-o-transition:      opacity .4s ease-in-out;
		transition:         opacity .4s ease-in-out;
		width: 200px;
		z-index: 99;

		a {
			background-color: #fff;
			border: 1px solid #eee;
			border-top: 0;
			font-size: 14px;
			font-size: 1.4rem;
			padding: 20px;
			position: relative;
			width: 200px;
			word-wrap: break-word;
		}

		.sub-menu {
			margin: -56px 0 0 199px;
		}
	}

	.menu-item:hover {
		position: static;

		> .sub-menu {
			left: auto;
			opacity: 1;
		}
	}
	
	> .first > a {
		padding-left: 0;
	}

	> .last > a {
		padding-right: 0;
	}

	> .right {
		float: right;
	}
}

@media only screen and (max-width: 1023px) {

	.js .genesis-nav-menu .sub-menu a,
	.js .genesis-nav-menu .sub-menu {
		width: 100%;
	}

	.genesis-nav-menu li,
	.site-header ul.genesis-nav-menu {
		float: none;
	}

	.genesis-nav-menu {
		text-align: center;
	}

	.js nav.nav-primary,
	.js .site-header nav  {
		display: none;
		position: relative;
	}

	.js .site-header nav .wrap {
		padding: 0;
	}

	.js nav.pagination {
		display: block;
	}

	// Menu Toggle
	.menu-toggle {
		border-top: 1px solid #eee;
		background-color: #fff;
		color: #333;
		display: block;
		line-height: 20px;
		margin: 0 auto;
		overflow: hidden;
		position: relative;
		right: 0;
		text-align: center;
		visibility: visible;
		width: 100%;
		z-index: 1000;

		.site-header & {
			border: 0;
		}

		&:before,
		&.activated::before {
			margin-right: 10px;
			text-rendering: auto;
			-webkit-transform: translate(0, 0);
			-ms-transform:     translate(0, 0);
			transform:         translate(0, 0);
		}
	}

	// Sub-Menu Toggle
	.sub-menu-toggle {
		border-width: 0;
		background-color: #fff;
		color: #333;
		display: block;
		float: right;
		margin: 0 auto;
		overflow: hidden;
		padding: 10px;
		position: absolute;
		right: 0;
		text-align: center;
		top: 0;
		visibility: visible;
		z-index: 100;

		&:hover,
		&:focus {
			border-width: 0;
		}

		&::before {
			display: inline-block;
			text-rendering: auto;
			-webkit-transform: translate(0, 0);
			-ms-transform:     translate(0, 0);
			transform:         translate(0, 0);
		}

		&.activated::before {
			content: "\f142";
		}
	}

	.js .nav-primary .genesis-nav-menu .menu-item,
	.js .site-header .genesis-nav-menu .menu-item {
		display: block;
		float: none;
		margin: 1px 0;
		position: relative;
		text-align: left;
	}

	.js .genesis-nav-menu .menu-item:hover {
		position: relative;
	}

	.js .nav-primary .genesis-nav-menu .menu-item a,
	.js .site-header .genesis-nav-menu .menu-item a {
		border: none;
		padding: 12px 0;
		width: 100%;
	}

	.js .genesis-nav-menu .menu-item > a:focus ul.sub-menu,
	.js .genesis-nav-menu .menu-item > a:focus ul.sub-menu .sub-menu {
		left: 0;
		margin-left: 0;
	}

	.js .genesis-nav-menu > .menu-item-has-children > a::after {
		content: none;
	}

	.js .genesis-nav-menu .sub-menu {
		border: none;
		clear: both;
		display: none;
		margin: 0;
		opacity: 1;
		padding-left: 15px;
		position: static;
		width: 100%;

		.sub-menu {
			margin: 0;
		}
	}

	.js nav button:hover,
	.js .menu-toggle:hover,
	.js .nav-primary {
		background-color: #fff;
		color: #000;
	}

	.js nav button:focus,
	.js .menu-toggle:focus {
		background-color: #fff;
		color: #c3251d;
	}

}
